@import "../../index.scss";

div#sports > div:last-child {
  p {
    color: #122c47;
    text-align: center;
    font-family: 'Padauk', sans-serif;
    font-weight: 400;
    width: 100%;
  }
  display: flex;
  max-width: $max-width;
  margin: auto;
  padding: 60px $site-padding;
  @include mobile {
    flex-direction: column-reverse;
    padding-top: 21px;
  }
  & > *:first-child {
    width: 100%;
    & > div:first-child {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @include mobile {
        flex-direction: column;
        align-items: flex-start;
      }
      & > div:first-child {
        display: flex;
        flex-direction: column;
        div {
          display: flex;
          align-items: center;
          &:last-of-type {
            margin-top: 26px;
            span {
              font-size: 21px;
            }
            * {
              font-family: 'Padauk', sans-serif;
              font-weight: 400;
              color: $dark-grey;
            }
          }
        }
        a {
          background: linear-gradient(#aeb0af, #717372, #8d8b8c);
          color: #ffffff;
          font-family: 'Padauk', sans-serif;
          font-weight: 400;
          border-radius: 10px;
          padding: 3px 22px;
          font-size: 18px;
          @include box-shadow(2px 3px 6px -2px #000000);
          margin-right: 14px;
          white-space: nowrap;
          display: flex;
          align-items: center;
          cursor: pointer;
          user-select: none;
          @include interactive-transition;
          svg {
            margin-left: 9px;
          }
          &.active {
            background: linear-gradient(#fbfbfb, #f9f9f9, #d5d5d5);
            color: $dark-grey;
          }
          &:hover {
            @include box-shadow(3px 4px 7px -2px #000000);
          }
        }
      }
      & > div:last-child {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-left: auto;
        max-width: 350px;
        @include mobile {
          margin: 0;
          margin-top: 20px;
        }
        span {
          color: $dark-grey;
          font-size: 18px;
          font-family: 'Padauk', sans-serif;
          font-weight: 400;
        }
        label {
          display: flex;
          justify-content: space-between;
          margin-bottom: 9px;
        }
        div {
          display: flex;
          align-items: center;
          justify-content: space-between;
          .rangeslider {
            height: 6px;
            position: relative;
            background: linear-gradient(#929292, #b3b3b3);
            width: 100%;
            touch-action: none;
            margin: 0 11px;
            &, .rangeslider__fill {
              @include box-shadow((inset 0px 0px 1px 0px #000000, 0px 0px 2px 0.5px #868686));
              border-radius: 20px;
            }
          }
          .rangeslider__fill {
            height: 100%;
            background-color: $red;
            display: block;
          }
          .rangeslider__handle {
            $width-height: 14px;
            left: 71px;
            width: $width-height;
            height: $width-height;
            border-radius: 100%;
            top: 50%;
            background: linear-gradient(#fbfbfb, #f9f9f9, #d5d5d5);
            position: absolute;
            cursor: pointer;
            transform: translate3d(-50%, -50%, 0);
            outline: none;
            @include box-shadow(0px 1px 3px -1px #000000);
            @include interactive-transition;
            &:hover, &:active {
              @include box-shadow(0px 2px 4px -1px #000000);
            }
          }
          .rangeslider-horizontal {
            display: block;
            width: 100%;
          }
        }
        .rangeslider__handle-tooltip {
          display: none;
        }
      }
    }
    & > div:nth-child(2) {
      display: flex;
      justify-content: space-between;
      @include mobile {
        flex-direction: column;
        margin-top: 13px !important;
      }
      & > div {
        width: 100%;
        &:first-of-type {
          margin-right: 23px;
        }
        & > div {
          $color: #122c47;
          display: flex;
          justify-content: space-between;
          border-bottom: 0.5px solid $color;
          padding: 8px 10px;
          span {
            font-family: 'Padauk', sans-serif;
            color: $color;
          }
          span:last-child {
            font-weight: 400; 
            font-size: 16px;
          }
          span:first-child {
            font-weight: 700;
            font-size: 20px;
          }
        }
      }
    }
    & > div:last-child {
      width: 100%;
      display: flex;
      margin-top: 56px;
      justify-content: center;
      a {
        $icon-margin: 20px;
        $color: #122c47;
        font-family: 'Padauk', sans-serif;
        font-weight: 700;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
        color: $color;
        padding: 1px 8px;
        padding-top: 4px;
        border-radius: 5px;
        user-select: none;
        @include interactive-transition;
        &:first-child {
          margin: 0;
          margin-right: $icon-margin;
        }
        &:last-child {
          margin: 0;
          margin-left: $icon-margin;
        }
        &.active, &:hover {
          background: $color;
          color: #ffffff;
        }
      }
    }
  }
  & > *:last-child {
    background: $grey;
    color: #ffffff;
    text-transform: uppercase;
    max-width: 200px;
    height: 500px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    margin-left: 65px;
    @include mobile {
      margin: 0 !important;
      max-width: 100%;
      height: initial;
      padding: 20px;
      margin-bottom: 22px !important;
    }
  }
}
